// 导航栏
nav {
    max-width: 1200px;
    margin: auto;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    padding: 25px 0;
    background-color: #060B17;
    @media screen and(max-width:1200px){
        max-width:100%;
    }
    @media screen and(max-width:576px){
        max-width:100%;
    }
    @media screen and(max-width:150px){
        max-width:100%;
        & > .nav_bottom > img{
            width: 15px;
            height: 15px;
        }
    }
    @media screen and(max-width:768px){
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-between;
        padding: 5px;
        max-width: 1000px;
        margin: 0;
    }
    //logo
    &>.nav_logo {
        margin-right: 70px;
        @media screen and(max-width:576px){
            margin-right: 50px;
        }
        & > h1 {
            margin: 0 auto 0 0;
            color:white;
        }
    }
    & > .nav_bottom{
        z-index: -1;
        display: none;
        // margin-left: 200px;
        @media screen and(max-width:768px){
            cursor: pointer;
            display: block;
            z-index: 1;
            & > img{
                width: 40px;
                height: 40px;
            }
        }
    }
    //下拉菜单
    & > .nav_la{
        display: flex;
        flex-direction: column;
        display: none;
        list-style: none;
        flex-basis: 800px;
        & a{
            color: white;
            display: block;
            text-align: end;
            padding: 8px 0;
        }
        @media screen and(min-width:699px){
            & a{
                display: none;
            }
        }
        & > .nav_la_box1,
        .nav_la_box2,
        .nav_la_box3{
            margin: 0;
            padding: 0;
            text-align: end;
            width: 653px;
            height: 34px;
            // margin: 10px 0;
            & > a{
                color: white;
            }
        }
    }
    & > .nav_linkbox{
        display: flex;
        flex-direction: row;
        align-items: center;
        @media screen and(max-width:768px){
            display: none;
        }
        // 导航栏包裹的a标签字体
        &>.nav_link {
            display: flex;
            flex-direction: row;
            margin: 0px 180px 0px 100px;
            @media screen and(max-width:1106px){
                margin: 0;
            }
            & > .nav_link_a:hover,
            .nav_link_b:hover,
            .nav_link_c:hover{
                    transition: all;
                    font-weight: 700;
            }
            & > .nav_link_a::before{
                    display: block;
                    content: attr(title);
                    font-weight: bold;
                    height: 0;
                    overflow: hidden;
                    visibility: hidden;
            }
            & > .nav_link_b,
            .nav_link_c,
            .nav_link_a{
                width: 100px;
                height: 22px;
                cursor: pointer;
            }
            & a {
                color: white;
                transition: 1ms;
                display: inline-block;
            }
        }
        &>.nav_img {
            display: flex;
            align-items: center;
            margin-left: 60px;
            @media screen and(max-width:825px){
                margin: 0;
            }
            &>a {
                margin: 0 5px;
                transition: all 1s;
            }
        }
    }
}

@primary-color: #F8F9F9;@layout-header-background: #060B17;@input-color: #FFFFFF;