footer{
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    width: 100%;
    flex-direction: row;
    justify-content: space-between;
    border-top: 0.5px solid #141C30;
    padding: 40px 20px;
    @media screen and(max-width:1268px){
        max-width:100%;
    }
    @media screen and(max-width:800px){
        max-width:100%;
    }
    @media screen and(max-width:576px){
        max-width:100%;
        flex-direction: column;
    }
    & > .footer_a{
        margin:auto;
        @media screen and(max-width:576px){
            margin-bottom: 10px;
        }
        & > p {
            color:white;
        }
    }
    & > .footer_b{
        display: flex;
        flex-direction: column;
        & > .footer_b1{
            color: white;
            margin-bottom: 10px;
            @media screen and(max-width:576px){
                text-align: center;
            }
        }
        & > .footer_b2{
            margin-top: 20px;
            margin-bottom: 10px;
            display: flex;
            flex-direction: row;
            & > .footer_input{
                margin: 0;
                padding: 0;
                width: 500px;
                background-color: #0E1527;
                @media screen and(max-width:576px){
                    width:100%;
                }
                @media screen and(max-width:768px){
                    width:100%;
                }
                @media screen and(max-width:992px){
                    width:100%;
                }
            }
            & > .footer_right{
                background-color:#D3F85A;
                margin-left: 10px;
                width: 150px;
                height: 60px;
                line-height: 60px;
                text-align: center;
                border-radius: 7px;
                cursor: pointer;
            }
            & > .footer_right:hover{
                background-color: #fff;
            }
        }
        & > .footer_b3{
            margin-top: 20px;
            margin-bottom: 10px;
            display: flex;
            flex-direction: row;
            & > .footer_1{

            }
            & > .footer_2{
                margin-left: 40px;
            }
            & a{
                color: white;
                text-decoration: underline;
            }
        }
    }
}

@primary-color: #F8F9F9;@layout-header-background: #060B17;@input-color: #FFFFFF;