//body部分
// banner
.container{
    display: flex;
    flex-direction: column;
    max-width: 1200px;
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    padding: 0 20px;
    @media screen and(max-width:1268px){
        max-width:100%;
    }
    @media screen and(max-width:768px){
        max-width:720px;
    }
    @media screen and(max-width:576px){
        max-width:100%;
    }
    // welcome
    & > .body_wlcom{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 40px;
        @media screen and(max-width:992px){
            display: flex;
            flex-direction: column;
        }
        & > .body_wlcom_font{
            margin-right: 60px;
            @media screen and(max-width:992px){
                display: flex;
                flex-direction: column;
                justify-content: center;
                margin: 0;
                padding: 0 0px;
            }
            @media screen and(max-width:768px){
                padding: 0;
                justify-content: flex-start;
                align-items: flex-start;
            }
            @media screen and(max-width:576px){
                padding: 0;
                align-items: flex-start;
            }
            & > .body_wlcom_h{
                margin-bottom: 55px;
                @media screen and(max-width:992px){
                    margin-bottom: 10px;
                }
                @media screen and(max-width:576px){
                    max-width:100%;
                }
               & > h1{
                   color: white;
               }
            }
            & > .body_wlcom_txt{
                & > p{
                    color: white;
                    text-align: justify;
                }
            }
        }
        //img
        & > .body_wlcom_img{
            display: flex;
            flex-direction: row;
            flex-wrap: wrap;
            margin-left: 180px;
            @media screen and(max-width:992px){
                margin-left: 0;
                justify-content: space-between;
                overflow: hidden;
            }
            @media screen and(max-width:768px){
                overflow: hidden;
               justify-content: space-between;
            }
            @media screen and(max-width:576px){
                justify-content: flex-start;
            }
            & > .body_wlcom_imga,
            .body_wlcom_imgb{
                display: flex;
                flex-direction: row;
                @media screen and(max-width:576px){
                    margin-top: 10px;
                }
                & > .end{
                    @media screen and(max-width:576px){
                        margin-left: 10px;
                    }
                }
                & > div{
                    @media screen and(max-width:992px){
                       margin: 10px 40px 10px 0;
                       & img{
                           width: 100%;
                           height: 100%;
                       }
                    }
                    @media screen and(max-width:768px){
                       margin: 5px 39px 5px 0;
                       & img{
                        width: 100%;
                           height: 100%;
                    }
                    }
                    @media screen and(max-width:576px){
                        margin: 0;
                       & img{
                           width: 150px;
                           height: 180px;
                       }
                    }
                    margin: 5px;
                }
            }
        }
    }
    // fair
    & > .body_fair{
        display: flex;
        flex-direction: column;
        background-color:#0E1527;
        border-radius: 10px;
        // font
        & > .body_fair_font{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin: 50px 0;
            @media screen and(max-width:576px){
                flex-direction: column;
                margin: 20px 0;
            }
            & > .body_fair_fonta{
                margin-left: 50px;
                @media screen and(max-width:576px){
                    margin: 0;
                }
                @media screen and(max-width:768px){
                    margin-left: 10px;
                }
                & > .body_fair_fonta_a{
                    & > h1{
                        color: white;
                    }
                    & > .body_fair_h1{
                        color: #D3F85A;
                        font-weight: bold;
                    }
                    & > .body_fair_h2{
                        @media screen and(max-width:576px){
                            font-size: 17px;
                        }
                        @media screen and(max-width:768px){
                            font-weight: 400;
                            font-size: 18px;
                        }
                    }
                }
                & > .body_fair_fonta_b{
                    color: white;
                }
            }
            & > .body_fair_fontb{
                margin-left: 200px;
                margin-right: 50px;
                padding-top: 30px;
                @media screen and(max-width:576px){
                    margin: 0;
                    padding-top: 10px;
                }
                @media screen and(max-width:768px){
                    margin: 0 0 0 10px;
                }
                & > p{
                    color: white;
                }
            }
        }
        // hr
        & > .body_hr{
           border-bottom: #141C30 1px solid;
           width: 90%;
           margin-top: 0;
           margin-bottom: 10px;
           @media screen and(max-width:576px){
              width: 95%;
           }
           @media screen and(max-width:768px){
              width: 98%;
           }
        }
        // button
        & > .body_fair_button{
            display: flex;
            flex-direction: row;
            justify-content: center;
            margin: 20px;
            @media screen and(max-width:576px){
                flex-direction: column;
                margin: 0;
            }
            @media screen and(max-width:768px){
               margin: 10px 0 0 0;
            }
            & > .body_fair_buttona{
                margin: 10px 25px;
                text-align: center;
                @media screen and(max-width:768px){
                   margin: 0 10px 0 10px;
                }
                & > h1{
                    color: white;
                    font-weight: bold;
                    width: 170px;
                    text-align: center;
                }
            }
            & > .body_fair_buttonb{
                margin: 10px auto;
                @media screen and(max-width:1200px){
                   margin: 0px 10px 0 10px;
                }
                @media screen and(max-width:992px){
                   margin: 0px 10px 0 10px;
                }
                @media screen and(max-width:768px){
                   margin: 0px 10px 0 10px;
                }
                @media screen and(max-width:576px){
                    margin: 0px 10px 0 10px;
                }
                & > p{
                    color: white;
                }
            }
            & > .body_fair_buttonc{
                display: flex;
                align-items: center;
                margin: 10px 25px;
                padding: 0 15px;
                cursor: pointer;
                max-width: 100%;
                background-color: #D3F85A;
                border-radius: 7px;
                @media screen and(max-width:992px){
                   margin: 10px 20px 10px 10px;
                }
                @media screen and(max-width:768px){
                   margin: 10px 20px 10px 10px;
                }
                @media screen and(max-width:576px){
                   margin: 10px 20px 10px 10px;
                }
                & > a {
                    color:#0E1527;
                    text-align: center;
                    border: 0;
                    font-weight: bold;
                    background-color: #D3F85A;
                    // margin: auto;
                    font-size: 14px;
                    @media screen and(max-width:576px){
                        width: 200px;
                        height: 60px;
                    }
                    @media screen and(max-width:768px){
                        width: 150px;
                        height: 60px;
                    }
                }
                & > a:hover {
                    text-decoration: underline;
                }
            }
        }
    }
    //THE SPECS
    & > .body_the{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 80px 0;
        @media screen and(max-width:576px){
            flex-direction: column-reverse;
            margin-top: 40px;
        }
        @media screen and(max-width:768px){
            align-items: center;
        }
        & > .body_the_img{
            margin-right: 30px;
            @media screen and(max-width:576px){
                margin-right: 0;
                & img{
                    width: 100%;
                    height: 100%;
                }
            }
        }
        & > .body_the_txt{
            display: flex;
            flex-direction: column;
            & > .body_the_txt_h1{
                margin-bottom: 20px;
                & > h1{
                    color: white;
                }
            }
            & > .body_the_txt_four1,
            .body_the_txt_four2,
            .body_the_txt_four3{
                & p{
                    color: white;
                }
                & > .body_the_txt_one{
                    display: flex;
                    flex-direction: row;
                    justify-content: space-between;
                    & > .body_the_txt_one1{
                        margin-right: 20px;
                    }
                }
                & > .body_the_txt_two,
                .body_the_txt_three{
                    margin-left: 30px;
                }
            }
        }
    }
    //WELCOME TO THE CLUB
    & > .body_clue{
        margin-bottom: 80px;
        & > .body_clue_h1{
            & > h1{
                color: white;
            }
        }
        & > .body_clue_txt{
            & > p{
                color: white;
            }
        }
        & > .body_clue_img{
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin-top: 20px;
            @media screen and(max-width:576px){
                flex-direction: column;
                align-items: center;
            }
            @media screen and(max-width:1200px){
                flex-wrap: wrap;
                justify-content: flex-start;
            }
            @media screen and(max-width:992px){
                overflow: hidden;
            }
            @media screen and(max-width:768px){
                justify-content: space-between;
            }
            & > .body_clue_img_card{
                width: 182px;
                height: 328px;
                display: flex;
                flex-direction: column;
                background-color: #0E1527;
                margin: 10px 20px;
                padding: 10px;
                border-radius: 7px;
                @media screen and(max-width:1200px){
                    // margin: 30px 65px 20px 0;
                    margin: auto;
                    width: 165px;
                    height: 350px;
                    padding: 5px;
                }
                @media screen and(max-width:992px){
                    // margin: 20px 50px 20px 0;
                    width: 130px;
                    height: 350px;
                }
                @media screen and(max-width:768px){
                    margin: 0px 50px 5px 0;
                   flex-direction: row;
                   align-items: center;
                   width: 100%;
                   height: 150px;
                }
                @media screen and(max-width:576px){
                    margin: 0px 50px 5px 0;
                   flex-direction: row;
                   align-items: center;
                   width: 328px;
                   height: 150px;
                }
                & > .body_clue_img_card1{
                    // margin: 30px 20px;
                    margin: auto;
                    overflow: hidden;
                    & > img{
                        width: 162px;
                        height: 162px;
                        @media screen and(max-width:992px){
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
                & > .body_clue_img_card2{
                    margin: 10px auto;
                    margin-bottom: 10px;
                    text-align: center;
                    & > span{
                        color: white;
                    }
                }
            }
        }
    }
    //THE BATHROOM
    & > .body_bathroom{
        display: flex;
        flex-direction: row;
        justify-content: flex-start;
        margin-bottom: 80px;
        @media screen and(max-width:992px){
            flex-direction: column;
        }
        @media screen and(max-width:768px){
            flex-direction: column;
        }
        @media screen and(max-width:576px){
            flex-direction: column;
        }
        & > .body_bathroom_txt{
            margin-right: 50px;
            @media screen and(max-width:768px){
                margin-right: 0px;
            }
            & > .body_bathroom_txt1{
                & > h1{
                    color: white;
                    font-weight: bold;
                }
            }
            & > .body_bathroom_txt2,
            .body_bathroom_txt3,
            .body_bathroom_txt3,
            .body_bathroom_txt4{
                & > p{
                    color: white;
                }
            }
            & > .b_h_1,
            .b_h_2,
            .b_h_3{
                margin: 40px 0;
                @media screen and(max-width:768px){
                    margin: 20px 0;
                }
            }
        }
        & > .body_bathroom_img{
            margin-left: 200px;
            text-align: center;
            @media screen and(max-width:1200px){
                margin-left:0;
            }
            @media screen and(max-width:992px){
                margin-left:0;
            }
            @media screen and(max-width:576px){
                margin-left:0;
            }
            // margin: 10px;
        }
    }
    //ROADMAP ACTIVATIONS
    & > .body_roadmap{
        display: flex;
        flex-direction: column;
        align-items: center;
        margin: 50px auto;
        & > .body_roadmap_txt{
            & h1{
                color: white;
                font-weight: bold;
            }
            &  p{
                color: white;
            }
            & > .body_roadmap_txt2{
                text-align: center;
            }
        }
        & > .body_roadmap_txtcolor{
            background-color: #0E1527;
            border-radius: 7px;
            padding:10px 0;
            width: 850px;
            @media screen and(max-width:576px){
                width:100%;
            }
            @media screen and(max-width:768px){
                width:100%;
            }
            @media screen and(max-width:992px){
                width: 100%;
            }
            text-align: center;
            & > p{
                color: white;
            }
        }
        & > .body_roadmap_txtnumber{
            display: flex;
            flex-direction: column;
            margin-top: 20px;
                & > .body_roadmap_txtnumber1{
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    margin: 10px 0;
                    & div{
                        color: white;
                    }
                    & > .b_r_n1{
                        color: #D3F85A;
                    }
                    & > .b_r_n2{
                        margin-left: 20px;
                        // text-decoration: line-through;
                    }
                }
        }
    }
    //COMMUNITY TOOLS
    & > .body_tools{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 40px;
        @media screen and(max-width:576px){
            flex-direction: column;
        }
        & > .body_tools_a{
            & > .body_tools_a1{
                & > h1{
                    color: white;
                    font-weight: bold;
                }
            }
            & > .body_tools_a2{
                margin-top: 20px;
                & > p{
                    color: white;
                }
            }
        }
        & > .body_tools_b{
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            & > .body_tools_b1,
            .body_tools_b2{
                display: flex;
                align-items: center;
                margin: 10px 0px 10px 10px;
                padding: 0 15px;
                cursor: pointer;
                max-width: 100%;
                background-color: #D3F85A;
                border-radius: 7px;
            }
            & > .body_tools_b1,
            .body_tools_b2{
                & > button{
                    text-align: center;
                    width: 150px;
                    height: 70px;
                    border: 0;
                    font-weight: bold;
                    background-color: #D3F85A;
                    margin: auto;
                    font-size: 14px;
                    cursor: pointer;
                }
            }
            & > .body_tools_b2:hover > button,
            & > .body_tools_b1:hover > button,
            & > .body_tools_b2:hover,
            & > .body_tools_b1:hover{
                background-color: #fff;
            }
        }
    }
    //THE TEAM
    & > .body_team{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin-bottom: 40px;
        @media screen and(max-width:576px){
            flex-direction: column;
        }
        @media screen and(max-width:768px){
            flex-direction: column;
        }
        & > .body_team_txt{
            & > .body_team_h{
                & > h1{
                    color: white;
                    font-weight: bold;
                }
            }
            & >.body_team_font{
                color: white;
            }
            & > .body_team_txtright{
                display: flex;
                flex-direction: column;
                & > .b_te_0{
                    color: white;
                    margin-left: 20px;
                }
                & > .b_te{
                    display: flex;
                    flex-direction: row;
                    justify-content: flex-start;
                    & > .b_te_img{

                    }
                    &  > .b_te_txtcolor{
                        margin-left: 10px;
                        & > p{
                            color: #D3F85A;
                        }
                    }
                    &  > .b_te_tx{
                        & > p{
                            color: white;
                        }
                    }
                }
            }
        }
        //img
        & > .body_team_img{
            display: flex;
            flex-direction: column;
            margin-left: 200px;
            @media screen and(max-width:992px){
                margin: 0;
            }
            @media screen and(max-width:768px){
                margin: 0;
            }
            @media screen and(max-width:576px){
                margin-left: 0;
            }
            & > .body_team_imga,
            .body_team_imgb{
                display: flex;
                flex-direction: row;
                & > div{
                    margin: 5px;
                    @media screen and(max-width:768px){
                        margin: 10px 50px 10px 0;
                        & img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                    @media screen and(max-width:576px){
                        margin: 2px;
                        & img{
                            width: 100%;
                            height: 100%;
                        }
                    }
                }
            }
        }
    }
    //VERIFIED SMART CONTRACT ADDRESS:
    & > .body_verified{
        display: flex;
        flex-direction: row;
        justify-content: center;
        background-color:#0E1527;
        padding: 25px;
        margin-bottom: 40px;
        border-radius: 7px;
        text-align: center;
        @media screen and(max-width:576px){
            flex-direction: column;
            padding: 0;
        }
        & > .body_verified_a{
            color: white;
        }
        & > .body_verified_b{
            color: #D3F85A;
            text-decoration: underline;
        }
    }
}

@primary-color: #F8F9F9;@layout-header-background: #060B17;@input-color: #FFFFFF;